* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
  color: black;
  height: 100%;
}

html,
body,
#root,
.container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 100%;
}

.movies .movie {
  width: 45%;
  background-color: #fff;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -16px 16px -6px rgba(0, 0, 0, 0.025);
  text-align: left;
}

.movie img {
  width: 100%;
  max-width: 130px;
  margin-right: 30px;
  position: relative;
  top: -50px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -16px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie_title,
.movie .movie_year,
.movie .movie_runtime {
  margin: 0;
  font-weight: 300;
}

.movie .movie_title {
  margin-bottom: 5px;
  font-size: 24px;
  font-weight: bold;
  color: #2c2c2c;
}

.movie .movie_genres {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: smaller;
  display: flex;
  margin: 5px;
}

.movie_genres li {
  margin-right: 10px;
}

.movie .movie_summary {
  font-size: smaller;
}
